@import url(https://fonts.googleapis.com/css?family=Ubuntu|Barlow);
@import url(https://fonts.googleapis.com/css?family=Ubuntu);
html {
    height: 100%;
    width: 100%;
}

body {
    background-color: #EEEEEE;
    color: darkslategray;
    font-family: 'Ubuntu', sans-serif;
    height: 100%;
    margin: 0;
    min-height: 100%;
    text-align: center;
    width: 100%;
}
@-webkit-keyframes logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
@keyframes logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

h2, h3 {
  color: #0a7ea3; }

h4 {
  color: #666666; }

h2, h3, h4, h5, h6 {
  font-family: 'Barlow', sans-serif;
  font-weight: bold; }

p {
  font-family: 'Ubuntu', sans-serif;
  font-size: 1rem;
  line-height: 20pt; }

p a, p a:hover {
  text-decoration: none;
  color: #de4c4b; }

.app {
  margin: 0 auto;
  clear: both;
  width: 100%;
  height: 95%; }

.app-header {
  padding: 20px; }
  .app-header h1 {
    text-transform: uppercase;
    font-family: 'Barlow', sans-serif;
    font-weight: bolder;
    font-size: 3rem; }
  .app-header span {
    color: #0a7ea3; }
  .app-header img {
    -webkit-animation: logo-spin infinite 15s linear;
            animation: logo-spin infinite 15s linear;
    height: 70px; }

.app-nav {
  min-height: 60px; }

.nav-item a {
  padding: 0.5em 1em;
  color: #666666;
  text-decoration: none; }

.main-content {
  padding: 0 4em; }

.card {
  margin: 2em 0; }

.app-footer {
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  clear: both; }
  .app-footer img {
    padding: 10px;
    height: 70px; }
  .app-footer p {
    color: lightslategray;
    font-size: small;
    margin-bottom: 0; }

@media (max-width: 540px) {
  .app-header h1 {
    font-size: 2rem; }
  .app-header img {
    height: 50px; }
  h4 {
    font-size: 1.25rem; }
  p {
    font-size: 1rem; }
  .main-content {
    padding: 0 2em; } }

.terminal-container {
  padding: 2rem 16rem 0 16rem; }

.terminal {
  background-color: #333;
  padding: 1rem 0 10rem 1rem; }

.css-typing p, .css-typing span, .css-typing h5 {
  text-align: left;
  font-family: 'Monaco', 'Courier New', sans-serif;
  font-size: 1.25rem;
  white-space: nowrap;
  overflow: hidden;
  color: white; }

.css-typing p {
  border-right: 0.75rem solid #EEE;
  color: #DDD; }

.type-in p {
  font-family: 'Ubuntu', sans-serif; }

span.user {
  font-family: 'Ubuntu', sans-serif;
  padding-right: 1rem;
  font-weight: bolder; }

span.orange {
  color: #e2640f;
  padding-right: 0; }

span.yellow {
  color: #b58e0e;
  padding-right: 0; }

span.green {
  color: #5c8205;
  padding-right: 0; }

span.blue {
  color: #6078db;
  padding-right: 0; }

.css-typing p {
  width: 12.5rem;
  opacity: 0;
  -webkit-animation: type 2s steps(14, end);
  animation: type 2s steps(14, end);
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards; }

@keyframes type {
  0% {
    width: 0; }
  1% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@-webkit-keyframes type {
  0% {
    width: 0; }
  1% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@media (max-width: 1360px) {
  .css-typing p, .css-typing span, .css-typing h5 {
    font-size: 1rem;
    margin-bottom: 0.25rem; }
  .css-typing p {
    width: 10.25rem;
    border-right: 0.6rem solid #EEE; }
  .terminal {
    padding: 0.5rem 0 9rem 0.5rem; }
  .terminal-container {
    padding: 2rem 8rem 0 8rem; } }

@media (max-width: 1100px) {
  .css-typing p, .css-typing span, .css-typing h5 {
    font-size: 0.8rem; }
  .css-typing p {
    width: 8.1rem;
    border-right: 0.5rem solid #EEE;
    line-height: 1.25rem; }
  span.user {
    padding-right: 0.75rem; } }

@media (max-width: 930px) {
  .css-typing p, .css-typing span, .css-typing h5 {
    font-size: 0.75rem;
    margin-bottom: 0.1rem; }
  .css-typing p {
    width: 7.75rem; }
  .terminal {
    padding: 0.5rem 0 7rem 0.5rem; }
  span.user {
    padding-right: 0.75rem; } }

@media (max-width: 760px) {
  .css-typing p {
    width: 7.45rem; }
  .terminal-container {
    padding: 2rem 4rem 0 4rem; }
  span.user {
    padding-right: 0.5rem; } }

@media (max-width: 630px) {
  .terminal-container {
    padding: 2rem 2rem 0 2rem; }
  .css-typing h5 {
    overflow-wrap: break-word;
    white-space: normal;
    line-height: 1.1rem; } }

.image-container {
  text-align: center;
  float: left;
  margin-right: 2rem; }

.profile-img {
  max-width: 8rem;
  width: 100%; }

.contact img {
  clear: both;
  margin: 0.5rem;
  width: 3rem;
  height: 3rem; }

.text-container, .image-container {
  margin-top: 1rem; }

@media (max-width: 700px) {
  .image-container {
    float: none;
    display: block;
    margin: 1rem; }
  .text-container {
    margin-top: 0; }
  .contact img {
    margin: 0.25rem;
    width: 2.5rem;
    height: 2.5rem; } }

.react-photo-gallery--gallery img {
  border: 2px solid darkgray; }

